/* Viewtitle.module.css */

.viewTitle {
    margin: 32px 0 32px 0;
    font-family: var(--font-family-ko);
    /* padding: 16px 16px 16px 16px; */
    text-align: right;
    padding: 16px;
}
.viewTitle h2 {
    font-size: var(--font-size-xxxl); 
    font-weight: 700;
}
.viewTitle h4 {
    font-size: var(--font-size-m); 
    font-weight: 500;
    margin-bottom: 16px;
    letter-spacing: -0.5px;
}
.viewTitle p {
    font-size: var(--font-size-ms); 
    font-weight: 400;
    /* letter-spacing: -0.5px; */
    
}

