/* Future.module.css */

.future {
    margin: 32px 0 32px 0;
    font-family: var(--font-family-ko);
    position: relative; 
    /* padding: 16px; */
}
.futureText {
    padding: 32px;
    color: var(--color-white1);
    position: absolute; 
    text-transform: none;
    
    z-index: 2; 

}
.futureText h3 {
    font-size: var(--font-size-l); 
    font-weight:600;
    
    margin:26px 0px 24px 0px;
    word-spacing: 4px;
    line-height: 1.2;

}
.futureText h4 {
    font-size: var(--font-size-ms); 
    font-weight: 500;
    letter-spacing: -0.8px;
  
    /* letter-spacing: -0.48px; */
}
.wrapper img {
    width: 100%; 
    /* display: block;  */
}