/* Special.module.css */

.special {
    text-transform: uppercase;
    margin: 64px 0 12px 0;
    position: relative;
  }
  
  .special h2 {
    padding: 16px;
    font-size: var(--font-size-xl);
    font-weight: 700;
    margin-bottom: 12px;
  }
  
  .sliderContainer {
    position: relative;
    width: 100%;
    height: 300px; /* 슬라이더의 높이를 고정 */
    overflow: hidden; /* 넘치는 콘텐츠 숨김 */
  }
  
  .specialList {
    position: relative;
    list-style: none;
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%; /* 슬라이더와 동일한 높이 */
  }
  
  .specialList li {
    position: absolute; /* 모든 li를 겹치게 설정 */
    top: 0;
    left: 0;
    width: 100%;
    height: 100%; /* 슬라이더 높이에 맞춤 */
    opacity: 0; /* 기본 상태는 투명 */
    transition: opacity 0.5s ease-in-out; /* 부드러운 전환 효과 */
  }
  
  .specialList li.active {
    opacity: 1; /* 활성화된 슬라이드만 보임 */
    z-index: 1; /* 다른 항목 위에 표시 */
  }
  
  .specialList li img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover; /* 이미지가 영역을 채우도록 */
  }
  
  .specialList li h4 {
    position: absolute;
    bottom: 16px; /* 이미지 하단에 텍스트 배치 */
    left: 16px;
    z-index: 2;
    font-size: var(--font-size-l);
    font-weight: 600;
    color: var(--color-white1);

    padding: 8px 12px;
    border-radius: 4px;
  }
  
  .moreBtn {
    position: absolute;
    position: relative;
    bottom: -40px; /* 슬라이더 하단 외부에 배치 */
    left: 50%;
    transform: translateX(-50%);
    text-transform: uppercase;
    font-size: var(--font-size-m);
    font-weight: 500;
    border-radius: 30px;
    padding: 10px 32px;
    background-color: var(--color-dark1);
    color: var(--color-white1);
    cursor: pointer;
    border: none;
    z-index: 2; /* 버튼이 슬라이더 위에 표시되도록 */
    padding: 15px 30px 15px 30px; /* 좌우 패딩 */
   
  }


.moreBtn::after {
    content: '→'; /* 오른쪽 화살표 문자 */
    font-size: var(--font-size-m); /* 화살표 크기 */
    font-weight: 600;
    display: inline-block;
    margin-left: 10px; /* 화살표와 텍스트 사이 여백 */
    transition: transform 0.3s ease-in-out; /* 부드러운 애니메이션 */
}

.moreBtn:hover::after {
    transform: translateX(5px); /* 마우스를 올렸을 때 화살표 이동 */
}