/* Gravity.module.css */

.gravity {
    margin: 120px 0 32px 0;
    font-family: var(--font-family-ko);
    /* padding: 16px 16px 16px 16px; */
    text-align: center;
    /* padding: 16px; */
}

.gravity {
    /* margin: 0; */
    /* padding: 0; */
    display: flex;
    justify-content: center;
    align-items: center;
    /* height: 100vh; */
    height: 80%;
    /* background-color: #f4f9fd; */
    /* font-family: "Poppins", sans-serif; */
  }
  
  .tagCanvas {
    width: 100%;
    height: 480px;
    position: relative;
    overflow: hidden;
    background-color: var(--color-dark1);
  }
  
  .marqueeContainer {
    position: absolute;
    top: 10px;
    left: 0;
    width: 100%;
    overflow: hidden;
    white-space: nowrap;
  }
  
  .marquee {
    display: inline-block;
    /* font-size: 180px; */
    /* font-size: 7.5rem; */
    font-size: 80px;
    font-size: 5rem;
    font-weight: bold;
    color: var(--color-white1);
    text-transform: uppercase;
    margin: 0;
    animation: marquee 18s linear infinite;
  }
  
  @keyframes marquee {
    0% {
      transform: translateX(0);
    }
    100% {
      transform: translateX(-100%);
    }
  }
  
  .tag {
    position: absolute;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    padding: 10px 30px;
    padding: 5px 20px;
    /* font-size: 40px; */
    font-size: 16px;
    font-size: var(--font-size-m);
    text-transform: uppercase;
    color: var(--color-dark1);
    background-color: var(--color-white1);
    font-weight: 800;
    border-radius: 50px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    pointer-events: none;
  }
  
  .tag:nth-child(3) {background-color: #4672DE;}
  .tag:nth-child(5) {background-color: #59FABD;}
  .tag:nth-child(10) {background-color: #FBD35A;}
  .tag:nth-child(12) {background-color: #FF775F;}
