/**** css 변수 ****/
/* 전역 변수 */
:root {/* 선언(설정) */
  /* --font-family-ko : 'SpoqaHanSans', -apple-system, BlinkMacSystemFont, system-ui, Roboto, "Helvetica Neue", "Segoe UI", "Apple SD Gothic Neo", "Noto Sans KR", "Malgun Gothic", "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", sans-serif; */
  --font-family-ko : "Poppins", -apple-system, BlinkMacSystemFont, system-ui, Roboto,  Georgia, 'Times New Roman', Times, serif;
  


  --font-size-xxxxl: 2.987rem; /* 47.8px */ 
  --font-size-xxxl: 2.487rem; /* 39.8px */
  --font-size-xxl: 2.075rem; /* 33.2px */
  --font-size-xl: 1.725rem; /* 27.6px */
  --font-size-l: 1.438rem; /* 23px */
  --font-size-ml: 1.2rem; /* 19.2px */
  --font-size-m: 1rem; /*** 16px ***/
  --font-size-ms: 0.875rem; /* 14px */
  --font-size-xs: 0.694rem; /* 11.1px */
  --font-size-xxs: 0.606rem; /* 9.7px */

  --color-dark1: #02040F;
  --color-dark2: #343A40;
  --color-dark3: #666666;

  --color-white1: #ffffff;
  --color-white2: #CCCCCC;

  
  --color-blue: #4672DE;
  --color-green: #59FABD;

  --color-yellow: #FBD35A;
  --color-orange: #FE6C2D;




  --line-height: 1.6;

  --spacing-xlg: 2.5rem;/* 40px */
  --spacing-lg: 2.125rem; /* 34px */
  --spacing-md: 1.25rem; /* 20px */
  --spacing-sm: 1.125rem; /* 18px */ 
  --spacing-xsm: 0.625rem; /* 10px */
}


/*****  모바일 390 기준 양옆 마진 16px 
/ pc 1900 기준 양옆 마진 250px   *****/

/**** global style ****/
/* 부드러운 스크롤 */
html {
  scroll-behavior: smooth;
}
/* 드래그 */
html ::selection {
  background-color: var(--color-white2);
  color: var(--color-dark1);
}
/* 스크롤바 */
html ::-webkit-scrollbar {
  width: 10px;
}
html ::-webkit-scrollbar-track {
  background-color: var(--color-white1);
}
html ::-webkit-scrollbar-thumb {
  background-color: var(--color-dark3);
  box-shadow: 0 1px 10px var(--color-white2);
  /* border: 1px solid var(--color-dark1); */
  /* border-radius: 10px; */
}

.flex-container {
  display: flex;
  align-items: center;
}
.grid-container {
  display: grid;
}
.margin-sm {
  margin: 0 var(--spacing-sm);
}
.margin-md {
  margin: 0 var(--spacing-md);
}
.margin-lg {
  margin: 0 var(--spacing-lg);
}
.padding-sm {
  padding: 0 var(--spacing-sm);
}
.padding-md {
  padding: 0 var(--spacing-md);
}
.padding-lg {
  padding: 0 var(--spacing-lg);
}





/**** body ****/
body {
  /* css 변수 적용 var() */
  font-family: var(--font-family-ko);
  color: var(--color-dark1);
  line-height: 1.5;
  word-break: break-all;
  word-wrap: break-word;
  text-transform: capitalize;
}


