
.Introtitle {
    font-family: var(--font-family-ko);
    padding: 16px 16px 32px 16px;
    text-align: center;
    margin-top: 120px;
    position: relative;
}

.introBtn {    
    font-size: 16px;
    font-weight: 700;
    border: 2px solid var(--color-dark1);
    background-color: var(--color-white1);
    padding: 2px 14px;
    border-radius: 32px;

    position: absolute;
    z-index: 1;
    left: 20%;
    top: 0px;
}

.title {
    font-size: 56px;
    font-weight: 700;
    line-height: 1.1;
    letter-spacing: -1.2px;
}

.subtitle {
    text-transform: uppercase;
    font-size: 20px;
    font-weight: 700;
}