
.footer {}

.footerMenu {
    font-size: var(--font-size-ms);
    font-weight: 700;
    display: flex;
    justify-content: space-between;
    padding: 32px 16px 0px 16px;
    
}
.footerMenu li { cursor: pointer; }
.footerMenu li:hover { 
    
    color: var(--color-orange);
}

.foterCenter {}
.footerLogo {
    display: flex;
    align-items: last baseline;
    padding: 70px 16px 0px 16px;
    line-height: 1.2;

}
.footerLogo h1 {
    font-size: var(--font-size-xxxl);
    font-weight: 700;
    margin-right: 12px;
}
.footerLogo h4 {
    font-size: var(--font-size-ms);
    font-weight: 700;
}

.footerSNS {
    display: flex;
    padding: 16px 16px 10px 16px;
}
.footerSNS li {
    margin-right: 16px;
    margin-right: 26px;
    cursor: pointer;
}


.footerSNS li img {
    width: 24px;
    
}

.foterCenter p {
    color: var(--color-dark3);
    font-size: var(--font-size-xs);
    padding: 0 16px 10px 16px;
}

.footerBot {
    padding: 24px 0 24px 0;
    background-color: var(--color-dark1);
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    justify-content: flex-end;

}
.footerBot img {
    width: 66px;
    width: 115px;
    margin-right: 18px;
}
