/* Exhibitionbox.module.css */
.exhibitionBox {
    padding: 0 16px;

}
.exhibitionBox img {

}
.exhibitionBox h4 {
    font-size: var(--font-size-m);
    font-weight: 600;
    padding: 10px 0;
}

.hallBtn {
    margin: 8px 0 38px 0;
    
}
.hallBtn span {
    border: 2px solid var(--color-dark1);
    font-size: var(--font-size-m);
    background-color: var(--color-white1);
    font-weight: 600;
    padding: 2px 16px;
    border-radius: 32px;
    cursor: pointer;

    position: relative; /* 위치 조정 가능 */
    margin-right: -8px; /* 오른쪽 여백을 음수로 조정하여 겹침 */
    z-index: 1; /* 겹치는 순서를 조정 */
}

.hallBtn span:first-child {

}

.hallBtn span.active {
    background-color: var(--color-dark1);
    color: var(--color-white1);
    border: 2px solid var(--color-dark1);
}


