
.header {
    width: 390px;
    margin: 0 auto;
    font-family: var(--font-family-ko);
    font-size: var(--font-size-m);
    color: var(--color-dark1);
    font-weight: 600;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 16px;
    position: fixed; /* 헤더를 화면 상단에 고정 */
    top: 0;
    left: 0;
    right: 0;
    background-color: var(--color-white1); 
    /* box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);  */
    z-index: 100;
    transition: transform 0.3s ease-in-out; 
  }
  



  .hidden {
    transform: translateY(-100%); /* 헤더를 위로 숨김 */
  }
  
  /* .header a {
    margin-left: 16px;
  }
   */

.logo {
    width: 138px;
}
.quick {
    margin-left: 16px;
    cursor: pointer;
}


/* 햄버거 메뉴 아이콘 스타일 */
.menu {
  
  position: relative;
  width: 24px;
  height: 12px;
 
  border-radius: 50%;
  display: inline-block;
  cursor: pointer;
}

.menu::before,
.menu::after {
  content: '';
  position: absolute;
  width: 100%;
  height: 3px;
  border-radius: 50px;
  background-color: var(--color-dark1);
  left: 0;
  transition: all 0.3s ease-in-out;
}


.menu::before {/* 위쪽 라인 */
  top: 0; 
}

.menu::after {/* 아래쪽 라인 */
  width: 18px; /* 아래쪽 라인 길이 */
  bottom: 0; /* 아래쪽 정렬 */
  left: auto; /* 왼쪽 기준 비활성화 */
  right: 0; /* 오른쪽 정렬 */
}

/* 중앙선 */
.menu span {
  position: absolute;
  top: 50%;
  left: 0;
  width: 100%;
  /* width: 10px; */
  height: 3px;
  background-color: var(--color-dark1);
  transform: translateY(-50%);
}

/* 중앙선 */
.menu span {
  position: absolute;
  top: 50%;
  left: 0;
  width: 100%;
  height: 3px;
  border-radius: 50px; /* 끝을 둥글게 */
  background-color: var(--color-dark1);
  transform: translateY(-50%);
  transition: opacity 0.3s ease-in-out;
}



/* 클릭 시 X 모양으로 변경 */
.open::before {
  top: 50%;
  transform: translateY(-50%) rotate(45deg); /* 대각선 */
}

.open::after {
  top: 50%;
  transform: translateY(-50%) rotate(-45deg); /* 반대 대각선 */
  width: 24px; /* 라인 길이 변경 */
}

.open span {
  opacity: 0; /* 가운데 라인 숨김 */
}