/* Introposter.module.css */

.posterText {
    font-family: var(--font-family-ko);
    /* padding: 16px 16px 16px 16px; */
    text-align: center;
    color: var(--color-white1);
    background-color: var(--color-dark1);
    padding: 16px;

}

.posterText strong {
    margin-right: 10px;
    font-weight: 600;
}
.posterText h4 {
    font-size: var(--font-size-m);
    /* color: var(--color-white2); */
    font-weight: 400;

}

.posterText p {
    font-size: var(--font-size-ms);
    color: var(--color-white2);
    font-weight: 400;

}
.posterText p strong {

    padding-left: 25px;
}
