/* Introkeywords.module.css */

.view {
    margin: 32px 0 32px 0;
    font-family: var(--font-family-ko);
    /* padding: 16px 16px 16px 16px; */
    text-align: center;
    padding: 16px;
}

.viewList {
    display: grid;
    overflow: hidden;
    grid-template-columns: repeat(2, 1fr); /* 2개의 동일한 컬럼 생성 */
    gap: 10px; /* 그리드 간격 설정 */
    padding: 0; /* ul의 기본 padding 제거 */
    list-style: none; /* 리스트 스타일 제거 */
    justify-items: center; /* 항목을 수평 중앙 정렬 */

 
}

.viewList li {
    text-align: center; /* 내부 콘텐츠 정렬 */
    overflow: hidden;
}

.viewList li h4 {
    margin-bottom: 38px; /* 제목과 이미지 사이 여백 */
    font-size: var(--font-size-ms); /* 제목 폰트 크기 */
    font-weight: 400; /* 굵기 */
}

.viewList li img {
    max-width: 100%; 
    height: auto;
    transition: transform 0.3s ease-in-out; 
    cursor: pointer;

}
.viewList li img:hover {
    transform: scale(1.1); 
  
}