/* Introkeywords.module.css */

.introKeywords {
    margin: 32px 0 32px 0;
    font-family: var(--font-family-ko);
    /* padding: 16px 16px 16px 16px; */
    /* text-align: center; */
    padding: 16px;

}
.introKeywords span {
    border: 2px solid var(--color-dark1);
    font-size: var(--font-size-ml);
    background-color: var(--color-white1);
    font-weight: 600;
    padding: 2px 16px;
    border-radius: 32px;
}

.introKeywords .keywordTop {
    margin-bottom: 16px;
}

.introKeywords .keywordTop span:first-child {
    background-color: var(--color-dark1);
    color: var(--color-white1);
}

.introKeywords .keywordTop span,
.introKeywords .keywordBot span {
    position: relative; /* 위치 조정 가능 */
    margin-right: -8px; /* 오른쪽 여백을 음수로 조정하여 겹침 */
    z-index: 1; /* 겹치는 순서를 조정 */
}